<template>
  <div class="main-padding">
    <el-card>
      <el-form :inline="true">
        <el-form-item>
          <el-button @click="add">新增</el-button>
        </el-form-item>
      </el-form>
    </el-card>
    <el-card style="margin-top: 20px;">
      <el-table border style="width: 100%" :data="list">
        <el-table-column label="特殊操作码" prop="code"></el-table-column>
        <el-table-column label="创建时间" prop="createDate"></el-table-column>
        <el-table-column label="操作" width="80">
          <template slot-scope="scope">
            <el-button @click="remove(scope.row.id)" type="danger" size="mini">
              删除
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
import { mapActions } from "vuex"

export default {
  data() {
    return {
      list: []
    }
  },
  mounted() {
    this.getList()
  },
  methods: {
    ...mapActions("authorizeCode", ["ASave", "AList", "ARemove"]),
    async getList() {
      let loading = this.$loading()
      this.list = (await this.AList()).data
      loading.close()
    },
    async remove(id) {
      let result = await this.ARemove({ id })
      if (result.status === 200) {
        this.$notify({ message: "成功" })
      } else {
        this.$notify({ message: result.message })
      }
    },
    add() {
      this.$prompt("请输入操作码", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      })
        .then(async ({ value }) => {
          let loading = this.$loading()
          let result = await this.ASave({ code: value })
          loading.close()
          if (result.status === 200) {
            this.$notify({ message: "成功" })
            this.getList()
          } else {
            this.$notify({ message: result.message })
          }
        })
        .catch(() => {})
    }
  }
}
</script>

<style type="text/less" lang="less" scoped></style>
